<template>
  <div class="container">
   <v-container fluid fill-height>
    <v-layout flex align-center justify-center>
     <v-flex>
      <!--<v-toolbar>
        <v-toolbar-title class="white--text">
          <h4>Login</h4>
        </v-toolbar-title>
      </v-toolbar>
      <div>-->
        <h1>Login</h1>
      <!--</div>-->
      <v-form
        v-model="valid"
        ref="form"
        lazy-validation
        @submit.prevent="loginEmail"
      >
        <!--<v-card elevation-1>
          <v-progress-linear
            :indeterminate="processing"
              background-color="transparent">
          </v-progress-linear>
          <v-card>
            <v-card-text class="pt-4">
              <div>-->
                <v-text-field
                  label="Usuario"
                  v-model="usuario"
                  :rules="[rules.required]"
                  required
                  outlined
                ></v-text-field>
                <v-text-field
                  label="Contraseña"
                  v-model="password"
                  min="8"
                  @click:append="e1 = !e1"
                  type="password"
                  :rules="passwordRules"
                  required
                  outlined
                  @keyup.enter.native="login"
                >
                </v-text-field>
            <!--  </div>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>-->
              <v-btn
                color="primary"
                :disabled="!valid"
                class="white--text"
                @click="login"
              >
                Ingresar
              </v-btn>
            <!--</v-card-actions>
          </v-card>
        </v-card>-->
      </v-form>
     </v-flex>
    </v-layout>
   </v-container>
  </div>
</template>
<script>
import { adminApp } from '@/services/api'
export default {
  data () {
    return {
      processing: false,
      valid: true,
      usuario: '',
      password: '',
      nombre: '',
      apellido: '',
      emailreg: '',
      emailRules: [
        v => !!v || 'Email es requerido.',
        v => /.+@.+\..+/.test(v) || 'Debe ser un email valido.'
      ],
      passwordRules: [
        v => !!v || 'Contraseña es Requerida.'
      ],
      rules: {
        required: value => !!value || 'Campo Requerido.'
      }
    }
  },
  methods: {
    async login () {
      if (this.$refs.form.validate()) {
        const { data } = await adminApp.login({
          username: this.usuario,
          password: this.password,
          area: 7
        })
        if (!data.data) {
          this.$toasted.error('Hubo un error al tratar de buscar las credenciales del usuario', { icon: 'error', duration: 5000 })
        } else if (data.data.length <= 0) {
          this.$toasted.error('Usuario y/o contraseña errados, usuario inactivo, o no pertenece al área de Jurídica', { icon: 'error', duration: 5000 })
        } else {
          this.$session.start()
          this.$session.set('logged', true)
          this.$session.set('usuarioid', data.data[0].usuarioid)
          this.$session.set('usuario', data.data[0].usuario)
          this.$session.set('nombre', data.data[0].nombreusuario)
          this.$session.set('sexo', data.data[0].sexo)
          this.$session.set('codigopersonal', data.data[0].codigopersonal)
          this.$router.push('/principalAdminApp')
        }
      }
    }
  }
}
</script>

